<template>
  <div class="songlistetails">
    <div>
      <van-nav-bar left-text="返回" fixed @click-left="goback" left-arrow />
    </div>
    <div
      class="songlist-top"
      :style="{ 'background-image': 'url(' + bgimg + ')' }"
    ></div>
    <div class="song-list-box">
      <div class="song-list-top">
        <div class="song-list-left">
          <div class="song-list-img-box playimg">
            <img
              class="auto-img"
              src="../assets/images/mv-play.png"
            />
          </div>
          <div class="song-list-playall">
            播放全部<span>(共{{ songListsData.length }}首)</span>
          </div>
        </div>
        <div class="song-list-collect">收藏</div>
      </div>
      <div
        class="song-list-top"
        v-for="(item, index) in songListsData"
        :key="index"
        @click="goPlayPage(item)"
      >
        <div class="song-list-left">
          <div class="song-list-img-box">
            <img class="auto-img" :src="item.al.picUrl" />
          </div>
          <div class="song-list-playall">
            <div class="song-list-content">{{ item.name }}</div>
            <div class="song-list-detail">
              {{ item.ar[0].name }} - {{ item.al.name }}
            </div>
          </div>
        </div>
        <div @click.stop="playmusic(item)" class="playbtn">
          <van-icon name="play-circle-o" />
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import "../assets/less/daily.less";
import { setupCookie } from "../assets/js/setupCookie";
// import {setupCookie} from "../assets/js/setupCookie"
export default {
  data() {
    return {
      songListData: [],
      songListDetailsData: [],
      songListsData: [],
      bgimg: "",
      // songListsData
      songlistid: "",
    };
  },
  created() {
    this.getmusicData();
  },
  computed: {},
  watch: {},
  methods: {
    //返回
    goback() {
      this.$router.go(-1);
    },
    // 获取歌曲详情
    getmusicData() {
      // // 验证通过
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner",
      });
      this.axios({
        method: "GET",
        url: "/recommend/songs",
        params: {
          cookie: setupCookie.getCookies(),
        },
      })
        .then((result) => {
          this.$toast.clear();
          if (result.data.code === 200) {
            this.songListsData = result.data.data.dailySongs;
            this.bgimg = result.data.data.dailySongs[0].al.picUrl;
            this.songListData = result.data.data.dailySongs.map((v) => {
              return v.id;
            });
          }
        })
        .catch((err) => {
          this.$toast.clear();
         console.error(err)
        });
    },
    //前往详情页面
    goPlayPage(item) {
      this.playmusicids = this.songListsData.map(v => {
        return v.id
      })
      this.$store.commit("setplaymusicids",this.playmusicids)
      this.$store.commit("setmusicid",item.id)

    },
    //播放不跳转
    playmusic(item) {
      this.playmusicids = this.songListsData.map(v => {
        return v.id
      })
      this.$store.commit("setplaymusicids",this.playmusicids)
      this.$store.commit("setmusicid",item.id)
    },
  },
};
</script>